  display: flex;
  flex-wrap: wrap;
  gap: 10px 41px;
  padding: 0;
  list-style: none;
}
@media (max-width: 1199px) {
  .nav {
    column-gap: 30px;
  }
}
@media (max-width: 767px) {
  .nav {
    display: block;
  }
}

.nav li {
  position: relative;
}

.nav__link {
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
}
@media (max-width: 767px) {
  .nav__link {
    font-size: 16px;
    line-height: 19px;
  }
}
.nav__link:hover::before {
  content: "";
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: #ffffff;
}
.nav__link:focus {
  border: 1px solid #ffffff;
